import React, { Component } from "react";

class Counter extends Component {

    constructor (props) {
        super(props)
        this.state = { count:0 }
    }
    

  render() {
    
    return (
      <div>
        <h1>Counter</h1>
        <p>current count: {this.state.count}</p>
        <button onClick={() => this.setState((previousState, props) => { return { count: previousState.count +1 }})}>plus</button>
        <button onClick={() => this.setState((previousState, props) => { return { count: previousState.count -1 }})}>minus</button>
      </div>
    );
  }  
}

export default Counter;



